import permision from "@/common/permission.js"
export default {
	data() {
		return {
			p_brand_index: 0,
			brandicon: '',
			describe: 'xxxxxxxxx',
			describe_length: 0
		}
	},
	onLoad() {
		this.describe_length = this.describe.length;
	},
	methods: {
		conInput() {
			this.tatVal = this.describe.length;
			this.describe_length = this.tatVal;
			if (this.tatVal == 120) {
				uni.showToast({
					icon: 'none',
					title: '最多只能输入120個字'
				});
			}
		},
		chooseImage() {
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				success: (res) => {
					let imgFile = res.tempFilePaths[0];
					console.log("获取的文件路径为:" + imgFile);
					this.brandicon = res.tempFilePaths[0];
				}
			})
		},
		imagepreview() {
			if (this.brandicon === '' || this.brandicon == null) {
				uni.showToast({
					title: '暂时没有图片'
				})
			} else {
				uni.previewImage({
					urls: [this.brandicon],
				})
			}
		}
	}
}
